



















































































































































































































import Vue from 'vue';
import { mapActions } from 'vuex';

import { Log, Transaction } from '@/interfaces/explorer';
import Constants from '@/constants';

export default Vue.extend({
  name: 'TransactionDetail',
  data: () => ({
    status: 1,
    hash: '',
    logs: [] as Array<Log>,
    args: [] as Array<string>,
    from: '',
    to: '',
    value: '...',
    data: '...',
    blockNumber: '...',
    blockHash: '',
    signature: '...',
    sighash: '...',
    transactionIndex: '...',
    topicDialog: false,
    argsDialog: false,
    isLoaded: false,
  }),
  methods: {
    ...mapActions({
      getTransactionByHash: 'explorer/getTransactionByHash',
    }),
    setData(transaction: Transaction) {
      if (transaction) {
        this.status = transaction.status;
        this.logs = transaction.logs;
        this.args = transaction.args;
        this.from = transaction.from;
        this.to = transaction.to;
        this.value = transaction.value;
        this.data = transaction.data;
        this.blockNumber = transaction.blockNumber.toString();
        this.blockHash = transaction.blockHash;
        this.signature = transaction.signature || '';
        this.sighash = transaction.sighash;
        this.transactionIndex = transaction.transactionIndex.toString();
      }
    },
  },
  async mounted() {
    this.hash = this.$route.params.hash;
    if (this.hash && this.hash.search(Constants.txhashRegex) === 0) {
      const data = await this.getTransactionByHash(this.hash);
      if (!data) {
        await this.$router.push(`/404?reason=${this.$i18n.t('TransactionNotFound')}&back=/explorer`);
      }
      this.setData(data);
    } else {
      await this.$router.push('/explorer');
    }
    this.isLoaded = true;
  },
  async beforeRouteEnter() {
    this.hash = this.$route.params.hash;
    if (this.hash && this.hash.search(Constants.txhashRegex) === 0) {
      const data = await this.getTransactionByHash(this.hash);
      this.setData(data);
    } else {
      await this.$router.push('/explorer');
    }
    this.isLoaded = true;
  },
});
